<template>

	<el-row :gutter="20" style="margin-top: -15px;">
		<el-col :span="2">
			<div style="margin-top:1px;">

			</div>
		</el-col>
		<el-col :span="4"> <img src="../assets/timg.jpg" height="40px"></el-col>
		<el-col :span="18">
			<div style="margin-top:0;">
				<table width="100%" style="text-align:left;">
					<tr>
						<th width="50%">
							<font size="2">{{list.name}}</font>
						</th>
						<th width="25%">
							<font size="1">{{list.time}}</font>
						</th>
						<th width="25%">
							<font size="1" color="coral">￥{{list.price}}</font>
						</th>
					</tr>
					<tr>
						<td colspan="3">
							<div style="margin-top: 0px;">
								<p>
									<font size="1" color="grey">{{list.content}}</font>
								</p>
							</div>
						</td>
					</tr>
				</table>
			</div>
		</el-col>

	</el-row>

</template>

<script>
	export default {
		props: ['content', 'index', 'list'],
		data: function() {
			return {
				del: ''
			}
		},
		methods: {
			clickInfo: function() {
				// alert(1)
				// this.$emit('delete',this.index)
			},
			filterShoppingList: function() {
				// `this` points to the vm instance            
				var index = this.index;
				var list = this.list;
				return list.filter(function(item) {
					return item.toLowerCase().indexOf(index.toLowerCase()) != -1
				});;
			}

		}
	}
</script>

